import React, {Component} from 'react';
import './child.css';
import '../../../common/icon/iconfont.css'
import Header from '../../../component/header/herder'
import Left from '../../../component/left/left'
import Modal from '../../../component/modal/modal'
import Confirm from '../../../component/confirm/confirm'
import axios from 'axios'


import NoLogin from '../../../component/Nologin/Nologin'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import {connect} from 'react-redux'


class Child extends Component {
    constructor(props, context) {
        super(props, context);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
        this.confirm= this.confirm.bind(this);
        this.cancel= this.cancel.bind(this);




        this.ID = this.ID.bind(this);
        this.IDOnFocus = this.IDOnFocus.bind(this);
        this.IDOnBlur = this.IDOnBlur.bind(this);

        this.URL = this.URL.bind(this);
        this.URLOnFocus = this.URLOnFocus.bind(this);
        this.URLOnBlur = this.URLOnBlur.bind(this);



        this.state = {
            data: [],
            userinfoState: false,
            leftState: this.props.iconState.iconState.iconState,
            Data:[],
            ModalMessage:'',
            showHideConfirm:false,
            showHideModal:false,
            addMP:false,
            upMP:false,
            img:'',

            ID:'',
            IDErrInfo: true,
            IDErrText: "",
            IDState:false,

            URL:'',
            URLErrInfo: true,
            URLErrText: "",
            URLState:false

        }
    }

    render() {
        return (
            this.state.userinfoState
                ?
                <div className="yc-Mobile">
                    <div className={this.state.addMP ? 'addMProduct':'addMProduct1'}>
                        <div className="addMProduct-top" >
                            <i className="iconfont icon-cuowu"
                               onClick={this.closeAddMP.bind(this)}></i>
                        </div>
                        <div className="addMProduct-bottom">
                            <div className="proInfo">填写热门搜索</div>
                            <div className="">
                                <div className="addMProduct-bottom-top">
                                    <input
                                        value={this.state.URL}
                                        onChange={this.URL}
                                        onFocus={this.URLOnFocus}
                                        onBlur={this.URLOnBlur}
                                        type="text"
                                        placeholder='请输入图片网络地址'/>
                                </div>
                                <div className="addMProduct-bottom-bottom">
                                    {this.state.URLErrText}
                                </div>
                            </div>
                            <div className="">
                                <div className="addMProduct-bottom-top">
                                    <input
                                        value={this.state.ID}
                                        onChange={this.ID}
                                        onFocus={this.IDOnFocus}
                                        onBlur={this.IDOnBlur}
                                        type="text"
                                        placeholder='请输入ID'/>
                                </div>
                                <div className="addMProduct-bottom-bottom">
                                    {this.state.IDErrText}
                                </div>
                            </div>
                            <button onClick={this.addMobileProduct.bind(this)}>确认提交</button>
                        </div>
                    </div>
                    <div className={this.state.upMP ? 'addMProduct':'addMProduct1'}>
                        <div className="addMProduct-top" >
                            <i className="iconfont icon-cuowu"
                               onClick={this.closeUpMP.bind(this)}></i>
                        </div>
                        <div className="addMProduct-bottom">
                            <div className="proInfo">修改热门搜索</div>
                            <div className="">
                                <div className="addMProduct-bottom-top">
                                    <input
                                        value={this.state.URL}
                                        onChange={this.URL}
                                        onFocus={this.URLOnFocus}
                                        onBlur={this.URLOnBlur}
                                        type="text"
                                        placeholder='请输入图片网络地址'/>
                                </div>
                                <div className="addMProduct-bottom-bottom">
                                    {this.state.URLErrText}
                                </div>
                            </div>
                            <div className="">
                                <div className="addMProduct-bottom-top">
                                    <input
                                        value={this.state.ID}
                                        onChange={this.ID}
                                        onFocus={this.IDOnFocus}
                                        onBlur={this.IDOnBlur}
                                        disabled="disabled"
                                        type="text"
                                        placeholder='请输入ID'/>
                                </div>
                                <div className="addMProduct-bottom-bottom">
                                    {this.state.IDErrText}
                                </div>
                            </div>
                            <button onClick={this.UpMobileProduct.bind(this)}>确认提交</button>
                        </div>
                    </div>
                    <Header/>
                    <Left/>
                    <div id="contentMobile"
                         className={this.state.leftState ?
                             "rightMoveMobile1" : "content-Mobile1"}>
                        <div className="">
                            <div className="SPGL">
                                商品管理
                            </div>
                            <div className="Mobile-addProduct"
                                 onClick={this.showAddMP.bind(this)}>
                                <p className="">新增商品</p>
                            </div>
                            <div className="MobileChild">
                                <div className='MobileChild-div'>
                                    {this.state.Data.map((item, index) => {
                                        return <div key={index} className='MobileChild-top'>
                                            <div className="MobileChild-top-left">
                                                {item.goods}
                                            </div>
                                            <div className="MobileChild-top-right">
                                                <p className="MobileChild-top-right-delete"
                                                   onClick={this.DeMobileProduct.bind(this,item.id)}>
                                                    删除
                                                </p>
                                                <p className="MobileChild-top-right-update"
                                                   onClick={this.showUpMp.bind(this,item.goods,item.id)}>
                                                    修改
                                                </p>
                                            </div>
                                        </div>
                                    })}
                                </div>



                            </div>
                        </div>
                    </div>
                    <Modal
                        ModalMessage={this.state.ModalMessage}
                        showHideModal={this.state.showHideModal}/>
                    <Confirm
                        cancel={this.cancel}
                        confirm={this.confirm}
                        showHideConfirm={this.state.showHideConfirm}/>

                </div>
                :
                <NoLogin/>

        );
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            leftState: nextProps.iconState.iconState.iconState,
        });
    }

    componentWillMount() {
        this._getMobile();
        this._getMobileInfo()


    };

    _getMobile() {
        let NowUserStates = localStorage.getItem("UserStates");
        NowUserStates = JSON.parse(NowUserStates);

        if (NowUserStates === null) {
            console.log(this.state.userinfoState);
            this.setState({
                userinfoState: false,
            })
        } else if (NowUserStates === "1") {
            this.setState({
                userinfoState: true
            })
        }

    };

    _getMobileInfo() {
        axios.post("/admGetSPSU")
            .then((res) => {
                this.setState({
                    Data: res.data
                })
            })
            .catch((err) => {
                console.log(err)
            })
    }

    ID(e) {
        let value = e.target.value;
        this.setState({
            ID: value
        });


    }

    IDOnFocus() {
        this.setState({
            IDErrInfo: false,
            IDErrText: "请输入ID"
        })
    }

    IDOnBlur(e) {
        let value = e.target.value;
        if (value.length === 0) {
            this.setState({
                IDErrInfo: true,
                IDErrText: "ID不能为空"
            })
        }
        else {
            this.setState({
                IDErrInfo: true,
                IDState:true,
                IDErrText: " "
            })
        }

    }


    URL(e) {
        let value = e.target.value;
        this.setState({
            URL: value
        })

    }

    URLOnFocus() {
        this.setState({
            URLErrInfo: false,
            URLErrText: "请输入网络地址"
        })
    }

    URLOnBlur(e) {
        let value = e.target.value;
        if (value.length === 0) {
            this.setState({
                URLErrInfo: true,
                URLErrText: "图片地址不能为空 ",
            })
        }
        else {
            this.setState({
                URLErrInfo: true,
                URLState:true,
                URLErrText: " "
            })
        }

    }



    closeAddMP() {
        this.setState({
            addMP: false
        })

    }

    closeUpMP() {
        this.setState({
            upMP: false
        })
    }

    showAddMP() {
        this.setState({
            addMP: true
        })
    }

    showUpMp(goods,id) {
        this.setState({
            upMP: true,
            URL:goods,
            ID:id
        })
    }

    addMobileProduct() {
        if (this.state.IDState === true && this.state.URLState === true) {
            axios.post('/admAddSPSU', {
                img: this.state.URL,
                id: this.state.ID
            })
                .then((res) => {
                    if (res.data === '1') {
                        this.setState({
                            ModalMessage: "添加成功",
                            showHideModal: true
                        });
                        const that = this;

                        function a() {
                            that.setState({
                                ModalMessage: "",
                                showHideModal: false
                            });
                            that.props.history.go(0)
                        }


                        setTimeout(a, 2000);


                    }
                    else if (res.data === '-1') {
                        this.setState({
                            ModalMessage: "添加失败",
                            showHideModal: true,
                        });
                        const that = this;

                        function b() {
                            that.setState({
                                ModalMessage: "",
                                showHideModal: false,
                            });
                        }


                        setTimeout(b, 2000);

                    }

                })
                .catch((err) => {
                    console.log(err)
                })
        }
        else {
            this.setState({
                ModalMessage: "请确认填写信息",
                showHideModal: true
            });
            const that = this;

            function a() {
                that.setState({
                    ModalMessage: "",
                    showHideModal: false
                });
            }


            setTimeout(a, 2000);

        }
    }

    DeMobileProduct(id) {
        this.setState({
            showHideConfirm:true,
            ID:id
        });
    }

    confirm = value => {
        if(value===true){
            axios.post('/admDeleteSPSU', {
                id: this.state.ID
            })
                .then((res) => {
                    if (res.data === '1') {
                        alert( '删除成功');
                        this.props.history.go(0)
                    }
                    else if (res.data === '-1') {
                        alert( '删除失败')
                    }

                })
                .catch((err) => {
                    console.log(err)
                })
        }

    }


    cancel = value => {
        this.setState({
            showHideConfirm: value
        })
    }


    UpMobileProduct(img) {
        if (this.state.URLState === true) {
            axios.post('/admUpdateSPSU', {
                img: this.state.URL,
                id: this.state.ID
            })
                .then((res) => {
                    if (res.data === '1') {
                        this.setState({
                            ModalMessage: "修改成功",
                            showHideModal: true
                        });
                        const that = this;

                        function a() {
                            that.setState({
                                ModalMessage: "",
                                showHideModal: false
                            });
                            that.props.history.go(0)
                        }


                        setTimeout(a, 2000);


                    }
                    else if (res.data === '-1') {
                        this.setState({
                            ModalMessage: "修改失败",
                            showHideModal: true,
                        });
                        const that = this;

                        function b() {
                            that.setState({
                                ModalMessage: "",
                                showHideModal: false,
                            });
                        }


                        setTimeout(b, 2000);

                    }

                })
                .catch((err) => {
                    console.log(err)
                })
        }
        else {
            this.setState({
                ModalMessage: "请确认填写信息",
                showHideModal: true
            });
            const that = this;

            function a() {
                that.setState({
                    ModalMessage: "",
                    showHideModal: false
                });
            }


            setTimeout(a, 2000);

        }
    }



}


function mapStateToProps(state) {

    return {
        iconState: state
    }

}

function mapDispatchToProps(dispatch) {
    return {}
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Child)


